import axios from "axios";
const baseUrl = 'http://localhost:8080/Library_war_exploded'

const login = (user) => {
    return axios
        .post(`${baseUrl}/AdminServlet`, {
            username: user.username,
            password: user.password,
            flag: login
        })
        .then(response => {
            return response.data
        })
}

export default { login }